@import "~react-image-gallery/styles/css/image-gallery.css";
@import url('https://fonts.googleapis.com/css2?family=Gotu&display=swap');

.project-image {
  opacity: 1;
  display: block;
  transition: .5s ease;
  backface-visibility: hidden;
}
.project-image img{
  width: 300px;
  height: 300px;
}
.bodytext li{
  font-family: 'Roboto', sans-serif;
  padding-bottom: 15px;
  line-height: 150%;
  font-size: 1.08em;
}
h1,h2, h3{
  -webkit-font-smoothing: subpixel-antialiased;
}
p{
  font-family: 'Roboto', sans-serif;
  font-size: 1.08em;
  line-height: 150%;
}

hr{
  border-color: rgb(255, 173, 176);
  background-color: rgb(255, 173, 176);
  border-style: solid;
  margin-top: 2%;
  padding-top: 0px;
  margin-bottom: 20px;
  text-decoration: none;
}
h2{
  margin-bottom: 7px;
}
/* Thumbnail Stuff */
.codecontent .project-title{
  margin-top:20px;
}
.project-code{
  position: relative;
}
.content {
  transition: .5s ease;
  position: relative;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: left;
}
.codecontent{
  text-align: left;
  transition: none;
  opacity: 1;
}
.buttons{
  position: relative;
  left: 50%;
  top:-4%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  border-radius: 20px;
}
.buttons a{
  transition: .5s ease;
  opacity: 0;
  background-color:rgb(255, 242, 242);
  color: rgb(204, 111, 111);
  font-size: 16px;
  padding: 12px ;
  display: block;
  width: 88%;
}
.project:hover .buttons a{
  opacity: 1;
}

.project-title{
  color: rgb(204, 111, 111);
}
.project-description{
  color: rgb(204, 111, 111);
}
#archivedesc{
  text-align: center;
  padding-left: 250px;
  padding-right: 250px;
}
.project:hover .project-image {
  color: rgb(204, 111, 111);
  opacity: 0.3;
}


.project:hover .content {
  opacity: 1;
}
.project-title{
  margin-top: 30px;
  font-size: 20px;
}

.footer{
  margin-top: 100px;
  margin-bottom: 15px;
}
.footer p{
  text-align: center;
  font-size: 12px;
  font-style: italic;
  padding-top: 0px;
  padding-bottom: 0px;
  margin-top: 2px;
  margin-bottom: 2px;
}
.footer a{
  font-size: 138%;
}
.nochange a{
  font-size: 100%;
}
a{
  text-decoration: none;
  color: #c24a4a;
}
a:hover{
  animation-name: fade;
  animation-duration: .5s;
  color: rgb(94, 24, 24);
}
@keyframes fade {
  from {color: #c24a4a;}
  to {color: rgb(94, 24, 24)}
}
.projmargins{
  display: block;
  max-width: 700px;
  margin: auto;
}
.aboutmargins{
  display: block;
  max-width: 900px;
  margin: auto;
}
.projmargins h2{
  line-height:150%;
  text-align: left;
  margin-top: 45px;
  margin-bottom: 0px;
}
.projmargins h3{
  margin-top: 35px;
  margin-bottom:10px;
  text-align: left;
}

.projmargins h4{
  margin-bottom: 0px;
  margin-top: 40px;
  color: rgb(204, 111, 111);
}
.projmargins img{
  display: block;
  margin: auto;
  width: 690px;
}
.special{
  text-align: center;
}
.special img{
  width: 250px;
}
.center1{
  margin: auto;
}
h2.forty{
margin-top: 40px;
}
h2.none{
  margin-top:5px;
}
.forty h2{
margin-top: 40px;
}
.center{
  text-align: center;
}
.parent{
  display: flex;
   overflow: hidden;
   margin: 1em 0;
}
.col{
  display: flex;
   flex-direction: column;
   flex-basis: 100%;
   flex: 1;
   margin-right: 40px;
}
.three{
  display: flex;
}
.three .col img{
  width: 100%;
  margin-top:10px;
  -webkit-box-shadow: 0px 0px 22px -5px rgba(0,0,0,0.37);
}
.row{
  display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    max-width: 1300px;
    justify-content: center;
  margin: auto;
  }
.opening img{
  -webkit-box-shadow: 0px 0px 22px -5px rgba(0,0,0,0.37);
}

.col img{
  width: 80%;
  position: relative;
  margin-bottom: 15px;
}
.column img{
  position: relative;
  width: 70%;
}
.three p{
  width: 30%;
  float: left;

}
.three p:nth-child(2){
  margin-left:24px;
  margin-right:24px;
}
.three:after{
  content: "";
  display: table;
  clear: both;
  margin-bottom: 50px;
}
.bodytext{
  padding-left: 8%;
  padding-right: 8%;
}
.projmargins .row{
  flex-wrap: nowrap;
}

.project{
  margin-left: 80px;
  margin-right: 80px;
  margin-top: 20px;
}

.part1 img{
  margin-top: 35%;
  margin-bottom: 20px;
  margin-right: 20px;
  position: relative;
  display: inline-block;
  border-radius: 2%;
  box-shadow: 15px 15px rgb(255, 235, 183);
  width: 220px;
  height: auto;
}
.part2{
  position: relative;
  padding-left: 3%;
  padding-right: 3%;
  width: 50%;
}
.part2 h3{
  text-align: left; 
}
.navigation{
  display: flex;
  justify-content: space-between;
  line-height: 3em;
  padding-left: 10%;
  padding-right: 10%;
  margin-top: 4vh;
}
.navigation h1{
  font-family: 'Gotu', sans-serif;
  text-align: left;
  font-size: 2em;
  padding: 0px;
  margin: 0px;
}
.navigation h2{
  font-size: 20px;
  margin-top: 0px;
}
.navigation-sub{
margin-left: auto;
}
.navigation-sub a{
  margin-left: 3vw;
  display: inline;
  font-size: 1em;
}
.header{
  text-align: left;
}
.item{
  color: black;
  background-image: linear-gradient(rgb(255, 242, 242), rgb(255, 242, 242));
  background-repeat: no-repeat;
  background-size: 100% 0.5em;
  background-position: 0 88%;
  transition: background-size 0.25s ease-in;
}
.item:hover {
    background-size: 100% 88%;
}
/* header styling */
#headerimage{
  margin-top: 25px;
  margin-bottom: 25px;
}
.projheader{
  width: 700px;
  margin: auto;
}
.center1 img{
  margin-top: 20px;
  width: 900px;
  display: block;
  margin: auto;
}
.projheader h1{
  font-weight: 600;
  display: block;
  text-align: left;
  margin-bottom: 0px;
  margin-right: 30px;
