  font-size: 2.3em;
}
.projheader em{
  font-size: 1.3em;
  color: #ff7083;
  margin-top: 0px;
}
em{
  color: #ff7083;
}
.projheader h2{
  margin-top: 5px;
  padding-top: 0px;
  margin-bottom: 5px;
  font-size: 1.25em;
  color:black;
}
span{
  font-size: 2em;
  margin-bottom: 5px;
  display: block;
  margin:auto;
  text-align: center;
}
.projheader h3{
  font-size:1em;
  color:#ff7083;
  margin-top: 0px;
  margin-bottom: 5px;
  font-family: 'Gotu', sans-serif;
  text-rendering: optimizeLegibility; 
  text-align: left;
}
.projheader .row{
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  margin-bottom: 40px;
}
