#about{
  margin-top:0px;
}
@media (max-width: 900px) {
  .part1 img{
    margin-top: 10%;
  }
  .three p:nth-child(2) {
    margin-left: 0px;
    margin-right: 0px;
}
  .three p{
    width:100%;
  }
  .projheader h1{
    font-size: 32px;
  }
  .navigation-sub a:first-child{
margin-left:0px;
  }
  .navigation{
    display: block;
  }
  #archivedesc{
  padding-left: 0px;
