@import url('https://fonts.googleapis.com/css2?family=Gotu&display=swap');

.titledesc h3{
  margin-top: 10px;
  font-family: 'Gotu', sans-serif;
  text-align: left;
  font-size: 150%;
}
.titledesc #andmore{
  text-align: right;
}
.headerrow{
  display: flex;
  margin-top: 40px;
  margin-bottom: 40px;
  margin-left: 40px;
  justify-content:center;
  /* background-image: linear-gradient(to bottom right, #fff6f5, #ffffff);  */
}
.headerrow img{
  width: 200px;
  height: 200px;
  margin-right:2vw;
}
.titledesc {
  width: 770px;
}
.bodytextwide{
  padding-left: 10%;
  padding-right: 10%;
}
.bodytextwide .project{
  margin-left: 20px;
  margin-right: 20px;
}
.smalltitle p{
  margin-top: 0px;
  margin-bottom: 10px;
  font-size: 1.1em;
  text-align: left;
  font-family: 'Roboto', sans-serif;
}
@media (max-width: 900px) {
  .headerrow img{
    display:none;
  }
  .headerrow{
    margin:0px;
  }
}